      opacity: 0;
      visibility: hidden;
      z-index: 3;
      width: 280px;
      [dir="rtl"] & {
        left: unset;
        right: 0;
      }
      &.open {
        opacity: 1;
        visibility: visible;
      }
    }
    .job-sidebar {
      background-color: var(--light-slate);
      padding: 16px;
      height: 600px;
      overflow-y: auto;
      .faq-accordion.default-according {
